.mainup-ctn {
    background-color: #fefcfa;
     position: absolute;
    top:0;
    z-index: 50; 
    overflow: hidden;
}
.frm-title {
    display: flex;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 70;
}
.lyin{
    z-index: 60;
    top: -600px;
    left: -1960px;
    position: absolute;
    width: calc(100% + 1300px);
    height: calc(100% + 1300px);
    border-radius: 50%;
    background-color: rgb(255, 171, 15);

}
.titlein {
    margin-top: 80px;
    width: 50%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.h {
    font-size: 37px;
    font-weight: 600;
    color:white;
    margin-bottom: 60px;
}
.y{
    font-size: 17px;
    font-weight: 500;
    color:white;
}
.e {
    font-size: 15px;
    font-weight: 400;
    color:rgba(255, 255, 255, 0.887);
}
.frm {
    width: 50%;
    padding:0px 37px;
    margin-top: 170px;
}
.up-btn{
    padding: 7px 32px;
    border-radius: 8px;
    color: white;
    background-color: rgb(255, 18, 18);
    border: 2px rgb(255, 18, 18) solid;
    width: fit-content;
    height: fit-content;
    font-weight: 400;
    font-size: 14px;
    transition: all;
    transition-duration: .3s;
}
.up-btn:hover{
    background-color: inherit;
    color: rgb(255, 18, 18);
}
