.img-drop{
    position: absolute;
    left : 0;
    top: 0px;
    min-width:calc(100% + 270px);
    height: 500px;
    object-fit: cover;
    object-position: center;
    opacity: .3;
    filter: brightness(1);
    user-select: none;
}
.pfps-parent {
 position: absolute;
 top : 26px;
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: center;
 width: 100%;
}
.pfps-ctn {
    border-radius: 10px;
    position: relative;
    border: solid rgba(0, 0, 0, 0.233) 4px;
    width:40%;
    margin: 10px auto;
    height:400px;
    background-color:white;
    overflow: hidden;
}
.pfps-icn {
    object-fit:cover;
    object-position: center;
    margin-bottom: 20px;
    border-radius: 50%;
    width: 110px;
    height: 110px;    
}
.usrs {
    font-size: 18px;
    font-weight: 500;
    max-width:96%;
    text-align: center;
}
.id-parent {
    display: flex;
    align-items: center;
    width:fit-content;
    margin: 0 auto;
    margin-top: 20px;
    cursor: pointer;
    user-select: none;
}
.ids{
    font-size: 14px;
    font-weight: 400;
    margin-right: 20px;
}
.code {
    font-size: 14px;
    font-weight: 500;
}
.btn-ctn {
    width:fit-content;
    height:fit-content;
}
.fls-btn {
    padding:8px 26px;
    background-color: rgb(255, 18, 18);
    font-size: 14px;
    font-weight: 500;
    border-radius: 4px;
    color: white;
    transition: all;
    transition-duration: .4s;
    position: absolute;
    bottom: 20px;
    right:21px;
}
.fls-btn:hover{
    background-color: rgb(223, 5, 5);
}
.edit-btn-ctn {
    width: fit-content;
    height: fit-content;
    margin: 0 auto;
}
.edit-btn {
    margin:0 auto;
    margin-top: 47px;
    width:fit-content;
    height: fit-content;
    padding:9px 36px;
    background-color: rgb(227, 99, 13);
    font-size: 13px;
    font-weight: 500;
    border-radius: 4px;
    color: white;
    transition: all;
    transition-duration: .4s;
}
.edit-btn:hover{
    background-color: rgb(205, 101, 31);

}
.edit-btn-update{
    color: rgb(205, 101, 31);
    border: rgb(205, 101, 31) solid 2px;
    margin:0 auto;
    margin-top: 47px;
    width:fit-content;
    height: fit-content;
    padding:6px 36px;
    font-size: 13px;
    font-weight: 500;
    border-radius: 4px;
    transition: all;
    transition-duration: .4s;
    position: absolute;
    top : 80px;
    right: 10px;
}
.edit-btn-update:hover{
    background-color: rgb(227, 99, 13);
    color:white;

}

/*Recent */
.rec-ctn {
    padding: 0 10px;
    margin-top: 200px;
    
}
.rec {
    font-size: 26px;
    font-weight: 500;
    color:rgba(82, 78, 78, 0.673);

}
.rec-main {
    position: relative;
    width: 90%;
    height: 500px;
    background-color: blue;
    margin: 0 auto;
    margin-top: 30px;

}
.lyr-ctn {
    width:100%;
    height: 100%;
    position: absolute;
    background-color: white;
    overflow: hidden;
}
.ly {

    position: absolute;
    width:150%;
    height: 150%;
    border-radius: 50%;
    bottom: -9cm;
    left: 13cm;
     background-image: linear-gradient(to bottom, rgb(255, 171, 15) ,rgb(241, 184, 76),rgb(255, 167, 2));

}
.ly-favs {

    position: absolute;
    width:150%;
    height: 150%;
    border-radius: 50%;
    bottom: -9cm;
    left: 13cm;
background-color:rgb(201, 100, 32);
}
.imgs-ctn{
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: auto;
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 0;
} 
.rec-posts {
    width:100%;
    height:350px;
    object-position: center;
    object-fit: cover;
    transition: all;
    transition-duration: .3s;
    cursor: pointer;
}
.rec-posts:hover {
    filter: brightness(.5);
}

/*Fav*/
.favs-ctn {
    padding: 0 10px;
    margin-top: 200px;
    margin-bottom: 5px;
}

