.flx {
    margin: 10px 0;
    display: flex;
    justify-content: space-around;
}
.pic-post-ctn {
    width: 50%;
}

.pic-post{
    border-radius: 4px;
    min-width:500px ;
    max-height:1200px;
}

.data-post{
    width: 40%;
    height: 370px;
    background-color: white;
    overflow: hidden;
    position: relative;
    border-radius: 4px;
}
.layer-data {
    background-color: rgb(255, 171, 15);
    width:220px;
    height: 220px;
    border-radius: 50%;
    position:absolute;
    top: -130px;
    left: -100px;
}
.layer-data2 {
    background-color: rgb(255, 171, 15);
    width:220px;
    height: 220px;
    border-radius: 50%;
    position:absolute;
    bottom: -130px;
    right: -110px;
}
.icn {
    position: absolute;
    top: 10px; 
    right: 14px;
    color:   rgb(252, 0, 0);
    font-size: 30px;
    cursor: pointer;
}
.pfp-btn{
    position: absolute;
    bottom: 10px;
    
    left: 26px;
}
.pfp-ctn{
    user-select: none;
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    margin-left: 3px;
    padding: 5px 10px;
    
}
.pfp-post{
    user-select: none;
    width:50px;
    height: 50px;
    border-radius: 50%;
    border: solid rgba(80, 74, 74, 0.607) 3px;
    object-fit: cover;
    object-position: center;
    margin-right: 20px;
}
.ins-btn-post {
    margin: 0 10px;
    padding: 6px 26px;
    border: solid 2px rgb(255, 18, 18);
    font-size: 14px;
    font-weight: 500;
    border-radius: 4px;
    color: rgb(255, 18, 18);
    transition: all;
    transition-duration: .4s;

}
.ins-btn-post:hover {
    border: solid 2px rgb(201, 100, 32);
    color:white;
    background-color: rgb(201, 100, 32);

}
.ti-ctn {
    display: flex;
    position: absolute;
    top:120px;
    width:98%;
}
.ti{
    font-size: 15px;
    font-weight: 400;
    color:rgba(0, 0, 0, 0.694);
    min-width: fit-content;
}
.tle {
    font-size: 17px;
    font-weight: 500;
    margin:0 10px;
}
.desc{
    margin-top: 50px;
    width:80%;
    font-size: 16px;
    font-weight: 500;
    color:rgba(82, 82, 82, 0.744); 
    text-align: justify;
}


