.main-parent {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.fl-ctn {
    width: 45%;
    height: 78vh;
    overflow: auto;
    border-radius: 3px;
    margin: 13px;
    background-image: linear-gradient(to bottom ,white, rgb(250, 221, 162));
}
.main-ctn {
    margin-top: 50px;
}

.icn-ctn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 85px;
}
.fl {
    margin: 10px;
    font-size: 18px;
    font-weight: 400;
}
.icn-fri {
    color: rgba(132, 127, 127, 0.822);
    font-size: 50px;

}
.emt {
    color:rgba(101, 101, 101, 0.9);
    font-size: 15px;
    font-weight: 500;
}

.acc-ctn {
    width: 96%;
    height: 74px;
    margin: 0 auto;
    background-color: white;
    border-radius: 2px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
    margin-bottom: 21px;
    cursor: pointer;
    user-select: none;
}
.acc-img {
    width:46px;
    height: 46px;
    object-fit: cover;
    object-position: center;
    border-radius: 50%;
    border: solid rgba(253, 64, 64, 0.472) 2px;
}
.acc-name {
    font-size: 15px;
    font-weight: 600;
}

/*Srh */

.srh-main-ctn {
    margin-top: 10px;
}
.srh-ctn {
    width: 50%;
    margin: 20px;
}
.srh-icn {
    width: 80%;
    margin: 0 auto;
}
.title-srh {
    font-size: 18px;
    font-weight: 400;
    margin: 15px auto;
    width: fit-content;
    
}

.srh-ctn2 {
    margin:0 auto;
    position: relative;
    width:87%;
    height: 170px;
    background-color:rgb(255, 252, 252);
    border-radius: 8px;
    border: solid 1px rgba(147, 139, 139, 0.249)
}
.user-img{
    width: 100%;
    padding:26px 23px;
    display: flex;
    justify-content: space-between;
}

.btn-ctn {
    position: absolute;
    bottom: 0;
    right: 10px;
    display: flex;
    padding: 13px;
}
.fl-btn {
    padding:8px 26px;
    background-color: rgb(255, 18, 18);
    font-size: 14px;
    font-weight: 500;
    border-radius: 4px;
    color: white;
    transition: all;
    transition-duration: .4s;
}
.fl-btn:hover{
    background-color: rgb(223, 5, 5);
}
.ins-btn {
    margin: 0 10px;
    padding: 6px 26px;
    border: solid 2px rgb(255, 18, 18);
    font-size: 14px;
    font-weight: 500;
    border-radius: 4px;
    color: rgb(255, 18, 18);
    transition: all;
    transition-duration: .4s;
}
.ins-btn:hover {
    border: solid 2px rgb(201, 100, 32);
    color:white;
    background-color: rgb(201, 100, 32);

}
.acc-img-srh {
    width:60px;
    height: 60px;
    object-fit: cover;
    object-position: center;
    border-radius: 50%;
    border: solid rgba(253, 64, 64, 0.472) 2px;
}
.srh-emt {
    font-size: 80px;
    width:fit-content;
    color:rgba(101, 101, 101, 0.9);
    margin:100px auto;
}

