/**/

.header{
    z-index: 40;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding :32px 38px;
    background-color:white;
    position:sticky;
    top:0;
}
.navs {
    font-family: 'Ubuntu', sans-serif;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    user-select: none;
    margin-left: 30px;
    margin-right: 30px;
    color : rgb(252, 0, 0);
    transition: all;
    transition-duration: 1s;
    text-decoration:inherit  ;
}
.navs:hover{
    text-decoration: line-through rgb(252, 0, 0) 2px ;

}
.navs-i{
display: none;
}
.nav-ac{
    text-decoration: line-through rgb(252, 0, 0) 1px ;

}
.logo {
    font-family: 'Ubuntu', sans-serif;
    font-size: 25px;
    font-weight: 800;
    color :rgb(166, 72, 9)

}

/*Home*/

.content-ctn {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    display:grid;
    gap: 10px; 
    width: 100%;
}

.icon-home-ctn {
    z-index: 30;
    position: fixed;
    bottom: 48px;
    right: 20px;
    width: fit-content;
    height: fit-content;
    cursor : pointer;
    user-select: none;
    transition: all;
    transition-duration: .2s;
}
.icon-home-ctn:hover{
    bottom: 60px;

}
.icon-home{
    object-fit: cover;
    object-position: center;
    border-radius: 50%;
    width: 70px;
    height: 70px;
    border: solid rgba(101, 101, 101, 0.491) 1px;
    filter: brightness(.5);
}
.icon-lyer {
    z-index: 20;
    position: absolute;
    width: fit-content;
    height: fit-content;
    top:0;
    right: 0;
    left: 0;
    bottom: 10px;
    margin: auto;
    font-size: 70px;
    color: rgb(255, 166, 0);
    font-weight: 600;
}
.home-img {
    object-fit: cover;
    object-position: center;
    width: 520px;
    height: 370px;
    border-radius: 7px;
    transition:all;
    transition-duration: .3s;
    
}
.home-pfp {
    z-index: 20;
    object-fit: cover;
    object-position: center;
    background-color: green;
    width:55px;
    height: 55px;
    border-radius: 50%;
    bottom: -20px;
    left: -15px;
    border : solid 1px rgba(132, 119, 119, 0.908);

}
.home-img:hover{
    filter: brightness(.8);

}
.usr-icn-ctn {
    display: flex;
    padding: 20px 20px;
    background-image: linear-gradient( to right, rgb(254, 254, 254), rgb(255, 243, 220), rgb(255, 95, 95));
    border-radius: 5px;
}
.usr {
    font-size: 15px;
    font-weight: 500;
}
.fv-icn {
    font-size: 26px;
    color: white;
    cursor: pointer;
    user-select: none;
}

/*Post */
.img-pre {
    max-width:500px ;
    max-height:500px;
}
.post-title{
    font-size: 30px;
    font-weight: 800px;
    font-family: 'Poppins', sans-serif;
    margin-bottom: 100px;
    width:40%;
}
.img-pre-ctn {
    padding: 100px 145px;
    border-radius: 5px;
    color :rgb(166, 72, 9);
    font-weight: 500;
    font-size: 15px;
    background-color: rgba(218, 218, 218, 0.735);
    font-family: 'Poppins', sans-serif;

}
.text-title {
    font-size: 16px;
    font-weight: 400;
    font-family: 'Poppins', sans-serif;
    color :rgb(166, 72, 9);
}
.form-ctn {
    width: 60%;
    margin-top: 60px;
}

